import React,{Component} from 'react'
import { Form, Select, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;

export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            "id": "",
            "time": "",
            "userName": "",
            "userID": "",
            "content": "",
            "fileName": "",
            "fileLink": "",
            "loading": true
        };
    }

    componentWillMount() {
        let that = this;
        ajax({
            url: "delegateFund/receivedProject/getDetailInfo",
            method: "post",
            data: {
                id: this.props.id
            },
            callback: function(data) {
                let id = data.data.id,
                    userName = data.data.userName,
                    time = data.data.time,
                    content = data.data.content,
                    fileName = data.data.fileName,
                    fileLink = data.data.fileLink;
                that.setState({
                    loading: false,
                    id,
                    userName,
                    time,
                    content,
                    fileName,
                    fileLink
                })
            }
        });
    }

    return() {
        history.back();
    }

    render() {
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        {...formItemLayout}
                        label="投递人"
                    >
                        <div>{this.state.userName}</div>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="投递内容"
                    >
                        <div>{this.state.content}</div>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="投递时间"
                    >
                        <div>{this.state.time}</div>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="投递文件"
                    >
                        <a href={this.state.fileLink}>{this.state.fileName}</a>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


