import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin, Row, Col } from 'antd';
const { Content } = Layout;
import DetailForm from './detailForm.jsx'
export class Detail extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <DetailForm id={this.props.params.id} />
                </Content>
            </Layout>
        )
    }
}

module.exports = Detail;


